@import url(https://fonts.googleapis.com/css?family=Material+Icons|Source+Code+Pro|Source+Sans+Pro&display=swap);
:root {
  --color-bg-main: rgb(68,184,188);
  --color-text-secondary: rgb(13,17,91);
  --color-text-primary: rgb(34,34,34);
  --color-overlay: rgb(238,238,238);
  --color-accent: rgb(205,72,72);

  --font-body: 'Source Sans Pro', sans-serif;
  --font-heading: 'Source Code Pro', monospace;
}

html {
  height: 100vh;
}

a {
  text-decoration: none;
}

mark {
  background-color: rgb(205,72,72);
  background-color: var(--color-accent);
  color: rgb(13,17,91);
  color: var(--color-text-secondary);
}

body {
  margin: 0;
  width: 100vw;
  height: 100%;
  box-sizing: border-box;
  background-color: rgb(68,184,188);
  background-color: var(--color-bg-main);
  font-family: 'Source Code Pro', monospace;
  font-family: var(--font-heading);
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

#root {
  height: 100%;
}

.App {
    position: relative;
    height: 100%;
    display: grid;
    grid-template-areas:
    "header"
    "body";
    grid-template-rows: 60px 1fr;
    grid-template-columns: 100%;
    /*background: url("/img/cover_talkinghead.gif") 0% 0% / cover;*/
    background: var(--color-bg-main) 0% 0% / cover;
    transition: background 0.25s ease;
}

.App-header {
    grid-area: header;
    z-index: 3;
}

.App-body {
    z-index: 2;
    grid-area: body;
    overflow: hidden;
}

.App-video {
    position: absolute;
    top: 0;
    left: 0;
    object-fit: cover;
    width: 100%;
    height: 100%;
    pointer-events:none;
    z-index: 1;
    opacity: 0;
    transition: opacity 0.5s ease-in-out;
}

.NavBar {
    display: grid;
    align-items: center;
    height: 100%;
    grid-template-columns: 4fr 1fr;
    padding-right: 20px;
    overflow: visible;
}

.NavBar-dropdown {
    display: block;
    position: fixed;
    background-color: var(--color-accent);
    padding: 10px 16px;
    visibility: hidden;
    z-index: 5;
    overflow: hidden;
    pointer-events: auto;
}

.NavBar-dropdown a{
    margin-left: 0 !important;
}

.NavBar a{
    text-decoration: none;
    font-weight: 800;
    margin-left: 16px;
    font-size: large;
    color: var(--color-text-secondary);
    background-color: var(--color-accent);
}

.NavBar a:hover {
    color: var(--color-accent);
    background-color: var(--color-text-secondary);
    /*text-decoration: underline;*/
}

.NavBar-active {
    color: var(--color-accent) !important;
    background-color: var(--color-text-secondary) !important;
    /*text-decoration: underline;*/
}

.WorksOverview {
    height: 100%;
    display: grid;
    grid-template-rows: 100%;
    overflow-x: scroll;
}


.IntroPanel {
    align-self: center;
    display: flex;
    align-items: center;
    flex-direction: column;
    font-size: x-large;
    margin-left: 20px;
    margin-right: 20px;
}

.IntroPanel h1 {
    color: var(--color-text-secondary);
    /*background-color: var(--color-accent);*/
    font-weight: bold;
}

.IntroPanel span {
    width: 80%;
    text-align: right;
    -webkit-animation: 1s ease-in-out 1s infinite alternate bob;
            animation: 1s ease-in-out 1s infinite alternate bob;
}

@-webkit-keyframes bob {
    from { margin-left: 0; }
    to   { margin-left: 24px; }
}

@keyframes bob {
    from { margin-left: 0; }
    to   { margin-left: 24px; }
}

.ProjectPanel {
    position: relative;
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: flex-end;
    align-items: center;
}

.ProjectPanel h1 {
    color: var(--color-text-secondary);
    background-color: var(--color-accent);
}

.ProjectPanel-overlay {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: var(--color-overlay);
    opacity: 0.0;
}

.ProjectPanel-overlay-active {
    opacity: 0.7;
}

.ProjectDetails {
    height: 100%;
    overflow-y: scroll;
    padding-left: 20px;
    padding-right: 20px;
}

.ProjectDetails video {
    width: 100%;
    /*max-height: 480px;*/
    /*max-width: 720px;*/
}

/* This element defines the size the iframe will take.
   In this example we want to have a ratio of 25:14 */
.ProjectDetails-aspect-ratio {
    position: relative;
    width: 100%;
    height: 0;
    padding-bottom: 56.25%; /* The height of the item will now be 56.25% of the width. */
}

.ProjectDetails-aspect-ratio iframe {
    position: absolute;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
}

.ProjectDetails h1 {
    font-size: xx-large;
}

.ProjectDetails h2 {
    font-size: small;
}

.ProjectDetails-section {
    font-size: x-large;
    width: 100%;
    display: grid;
    grid-gap: 20px;
    grid-template-columns: 1fr 1fr 1fr;
    grid-template-rows: 60px 70px 1fr 1fr 1fr 1fr 1.1fr;
    grid-template-areas:
            "title . ."
            "subtitle . ."
            "a1 b1 c1"
            "d1 e1 f1"
            "g1 h1 i1"
            "a2 b2 c2"
            "d2 e2 f2";
}

.ProjectDetails-title {
    grid-area: title;
}

.projectDetails-subtitle {
    grid-area: subtitle;
}

.ProjectDetails-video1 {
    grid-area: a1-start / a1-start / e1-end / e1-end;
}

.ProjectDetails-description1 {
    grid-area: c1-start / c1-start /i1-end / i1-end;
}

.ProjectDetails-background1 {
    background-color: var(--color-text-secondary);
    grid-area: e1-start / e1-start / i1-end / i1-end;
}

.ProjectDetails-video2 {
    grid-area: b2-start / b2-start / f2-end / f2-end;
}

.ProjectDetails-description2 {
    grid-area: g1-start / g1-start /d2-end / d2-start;
}

.ProjectDetails-background2 {
    background-color: var(--color-text-secondary);
    grid-area: d2-start / d2-start / e2-end / e2-end;
}

@media screen and (min-width: 1131px) {

    .ProjectDetails-section {
        grid-template-rows: 60px 70px 1fr 0.1fr 1fr 1fr 1.1fr;
        font-size: xx-large;
    }

    .ProjectDetails-video1 {
        grid-area: a1-start / a1-start / h1-end / h1-end;
    }

    .ProjectDetails-description1 {
        grid-area: c1-start / c1-start /i1-end / i1-end;
    }

    .ProjectDetails-background1 {
        background-color: var(--color-text-secondary);
        grid-area: e1-start / e1-start / i1-end / i1-end;
    }

    .ProjectDetails-video2 {
        grid-area: b2-start / b2-start / f2-end / f2-end;
    }

    .ProjectDetails-description2 {
        grid-area: a2-start / a2-start /d2-end / d2-start;
    }

    .ProjectDetails-background2 {
        background-color: var(--color-text-secondary);
        grid-area: d2-start / d2-start / e2-end / e2-end;
    }
}

@media screen and (max-width: 719px) {
    .ProjectDetails-section {
        grid-template-columns: 0 1fr 0;
        grid-gap: 0;
        grid-template-rows: 60px 0.1fr 0.5fr 0.5fr 0.1fr 1fr 0.3fr;
    }
    .ProjectDetails-video1 {
        grid-area: b1;
    }

    .ProjectDetails-description1 {
        grid-area: e1;
    }

    .ProjectDetails-video2 {
        grid-area: h1;
    }

    .ProjectDetails-description2 {
        grid-area: b2;
    }

}

.About {
    display: flex;
    padding-left: 20px;
    padding-right: 20px;
    font-size: large;
    max-height: 100%;
    overflow-y: scroll;
}

.About p {
    white-space: pre-line;
}

.Contact {
    height: 100%;
    padding-left: 20px;
    padding-right: 20px;
    grid-gap:10px;
    display: grid;
    grid-template-areas:
    "message form .";
    grid-template-columns: 1fr 2fr 1fr;
    font-size: large;
}

@media screen and (max-width: 500px) {
    .Contact {
        grid-template-areas:
                "message form";
        grid-template-columns: 1fr 2fr;
    }
}

.Contact-message {
    grid-area: message;
}

.Contact-form {
    grid-area: form;
    align-self: center;
}

.Contact input{
    height: 30px;
    border: none;
    font-size: large;
    border-radius: 1px;
    background-color: var(--color-text-secondary);
    color: var(--color-accent);
    width: 100%;
}

.Contact textarea {
    width: 100%;
    height: 150px;
    border: none;
    font-size: large;
    border-radius: 1px;
    background-color: var(--color-text-secondary);
    color: var(--color-accent);
}

.Contact-success {
    width: 100%;
    height: 100%;
    display: flex;
    text-align: center;
    align-items: center;
    justify-content: center;
}

.Contact-error {
    display: none;
}

.Contact-error mark {
    background-color: red;
    color: yellow;
    font-weight: bold;
}

.Contact button {
    border: none;
    background-color: var(--color-accent);
    color: var(--color-text-secondary);
    cursor: pointer;
    font-weight: bold;
    font-size: large;
    float: right;
    height: 48px;
    width: 100px;
}

